:root {
  --color-black: #000;
  --color-white: #fff;
  --color-text: #474132;
  --color-gray: #fafafa;
  --color-border: #d1cec7;
  --color-accent: #edba4b;
  --color-ecru: #f9f6f0;
  --color-small-text: #756d59;
  --color-gradient: linear-gradient(270deg, #feca56 0%, #ebb949 51%, #feca56 100%);
  --shadow-main: 0 30px 50px rgba(221, 215, 198, 0.2);
  --shadow-second: 0 0 80px rgba(221, 215, 198, 0.1);
  --shadow-button-main: 0 50px 50px rgba(235, 185, 73, 0.2);
  --shadow-button-second: 0 50px 50px rgba(235, 185, 73, 0.1);
  --ff-main: "Manrope", sans-serif;
  --ff-second: "Trirong", sans-serif;
  --font-h1: 400 4.25rem / 140% var(--ff-second);
  --font-h2: 400 3.5rem / 150% var(--ff-second);
  --font-logo: 400 2rem / 125% var(--ff-second);
  --font-sm-title: 400 1.5rem / 150% var(--ff-second);
  --font-qa-title: 400 1.375rem / 150% var(--ff-second);
  --font-card-title: 600 1.25rem / 170% var(--ff-main);
  --font-2md: 400 1.125rem / 170% var(--ff-main);
  --font-md: 400 1rem / 110% var(--ff-main);
  --font-sm: 400 0.875rem / 110% var(--ff-main);
  --font-xsm: 400 0.75rem / 110% var(--ff-main);
  --animation-500ms: 0.5s ease-out;
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

