  line-height: 40px;
}
.menu__item {
  position: relative;
}
.menu__item::before {
  content: "";
  position: absolute;
  left: -16px;
  top: 50%;
  display: block;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  background: var(--color-gradient);
  transform: translateY(-50%);
}
